import * as React from 'react';
import CvCompleteImg from '../../../../../public/assets/images/portal/my-application/cv_complete_not_yet_apply.svg';
import { useNavigate } from 'react-router-dom';

export default function CvComplete() {
  const navigate = useNavigate();

  return (
    <React.Fragment>
      <div className="flex items-center justify-center">
        <img src={CvCompleteImg} alt="group_1" />
      </div>
      <h3 className="text-slate-900 font-bold text-lg">You haven't applied yet</h3>
      <p className="font-light text-slate-500 text-sm mt-2">Your heve completed your Curriculum Vitae, feel free to applied</p>

      <button onClick={() => navigate('/career')} className="font-semibold text-blue-700 mt-5">
        Lamar Pekerjaan
      </button>
    </React.Fragment>
  );
}
