import * as React from 'react';
import CvNotCompleteImg from '../../../../../public/assets/images/portal/my-application/cv_not_complete.svg';
import { useNavigate } from 'react-router-dom';

export default function CvNotCompplete() {
  const navigate = useNavigate();

  return (
    <React.Fragment>
      <div className="flex items-center justify-center">
        <img src={CvNotCompleteImg} alt="group_1" />
      </div>
      <h3 className="text-slate-900 font-bold text-lg">Your personal information is incomplete</h3>
      <p className="font-light text-slate-500 text-sm mt-2">Please complete the required information to continue with your application</p>

      <button onClick={() => navigate('/application-form')} className="font-semibold text-blue-700 mt-5">
        Edit Personal Informasi
      </button>
    </React.Fragment>
  );
}
