import * as React from "react";
import { Button, Dialog, Typography } from "@material-tailwind/react";
import warningImage from "../../../../../public/assets/images/warning.svg";

export type T_ModalState = {
  isOpen: boolean;
  mode: "draft" | "submit";
  loading: boolean;
}

type T_FormModal = {
  modalState: T_ModalState;
  setModalState: React.Dispatch<React.SetStateAction<T_ModalState>>
  action?: () => void;
  cancelAction?: () => void;
  className?: string;
};

const FormModal: React.FC<T_FormModal> = ({modalState, setModalState, action, cancelAction, className}) => {
  return (
  <Dialog size="md" open={modalState.isOpen}>
      <div className={"flex flex-col p-8 gap-y-4 text-black " + className}>
        <div className="flex flex-1 justify-center">
          <img
            src={warningImage}
            alt={"warningImg"}
            className={`h-[70px] object-center`}
          />
        </div>

        <div className="flex flex-col items-center">
          <Typography className="mb-2 text-center text-lg font-medium">
            {modalState.mode === "draft" 
            ? <>Save changes <span className="font-bold">as a Draft</span>?</> 
            : <>Are you sure you want to submit your answers?</>}
          </Typography>
          
          <Typography className="text-sm font-normal text-center text-gray-500">
            {modalState.mode === "draft"
            ? <>If you save this as a draft, you'll have the freedom to revisit and modify your answers at any time before final submission.</>
            : <>Please review them carefully before proceeding. Once submitted, your responses cannot be changed.</>}
          </Typography>
        </div>

        <div className="flex w-full mt-4 gap-2 justify-center">
          <Button
            color="gray"
            variant="outlined"
            size="sm"
            className="px-20 text-base border-gray-300 normal-case"
            onClick={() => {
              cancelAction && cancelAction();
              setModalState({ ...modalState, isOpen: false });
            }}
            disabled={modalState.loading}
          >
            Cancel
          </Button>
          <Button
            type="submit"
            color="blue"
            size="sm"
            className="px-20 text-base normal-case bg-primary-500"
            disabled={modalState.loading}
            loading={modalState.loading}
            onClick={action}
          >
            Confirm
          </Button>
        </div>
      </div>
    </Dialog>
  );
};

export default FormModal;
