import * as React from 'react';
import { ResultProviderProps, TResultContext } from './context.types';
import { initialValueResult, reducerResult } from './actions/resultAction';

export const ResultContext = React.createContext<TResultContext | undefined>(undefined);

export const useResultContext = () => {
  const context = React.useContext(ResultContext);
  if (context === undefined) {
    throw new Error('useResultContext must be used within a ResultProvider');
  }
  return context;
};

export const ResultProvider = ({ children }: ResultProviderProps) => {
  const [state, dispatch] = React.useReducer(reducerResult, initialValueResult);

  return <ResultContext.Provider value={{ state, dispatch }}>{children}</ResultContext.Provider>;
};
