import * as React from 'react';
import { TStepperApplication } from './StepperApplication.type';
import { NotSuitableIcon, PassedIcon, PendingIcon, ProgressIcon } from '../../assets/svg';
import { Typography } from '@material-tailwind/react';
import { FaCheck, FaRegCircle } from "react-icons/fa";
import { RiLoader2Line } from "react-icons/ri";
import { IoClose } from "react-icons/io5";
import { clsx } from 'clsx';

export default function StepperApplication({ list_stages }: TStepperApplication) {
  const lineClasses = {
    qualified: 'border-t-green-500',
    unqualified: 'border-t-[#4142D8]',
    disqualified: 'border-t-red-600',
    pending: 'border-t-gray-300',
  };

  const iconType = {
    qualified: <PassedIcon />,
    unqualified: <ProgressIcon />,
    disqualified: <NotSuitableIcon />,
    pending: <PendingIcon />,
  };

  const iconClasses = {
    qualified: 'bg-green-500',
    unqualified: 'bg-[#4142D8]',
    disqualified: 'bg-red-600',
    pending: 'bg-gray-300',
  };

  const badgeClasses = {
    qualified: 'bg-green-50 text-green-600 ',
    unqualified: 'bg-[#4142D8] text-white',
    disqualified: 'bg-red-600 text-white',
    pending: 'bg-white text-gray-500 border-dashed border border-gray-500',
  };

  const textClasses = {
    qualified: 'text-slate-600',
    unqualified: 'text-[#4142D8] font-semibold',
    disqualified: 'text-red-600 font-semibold',
    pending: 'text-gray-500',
  };

  const qualificationStatus = {
    stage_color: (status: string): string => {
      switch(status) {
        case "qualified":
          return "text-black font-normal";
        case "unqualified":
          return "text-indigo-500 font-bold";
        case "disqualified":
          return "text-danger-500 font-bold";
        default:
          return "text-gray-300 font-normal";
      }
    },
    status_color: (status: string): string => {
      switch(status) {
        case "qualified":
          return "bg-success-50 text-black font-normal";
        case "unqualified":
          return "bg-indigo-500 text-white font-bold";
        case "disqualified":
          return "bg-danger-500 text-white font-bold";
        default:
          return "bg-white text-gray-300 font-normal border border-dashed border-gray-300";
      }
    },
    line_color: (status: string): string => {
      switch(status) {
        case "qualified":
          return "bg-success-500";
        case "unqualified":
          return "bg-indigo-500";
        case "disqualified":
          return "bg-danger-500";
        default:
          return "bg-gray-300";
      }
    },
    baloon_color: (status: string): string => {
      switch(status) {
        case "qualified":
          return "bg-success-500";
        case "unqualified":
          return "bg-indigo-50 border border-indigo-500";
        case "disqualified":
          return "bg-danger-500";
        default:
          return "bg-gray-300";
      }
    },
    name: (status: string): string => {
      switch(status) {
        case "qualified":
          return "Qualified";
        case "unqualified":
          return "On Going";
        case "disqualified":
          return "Disqualified";
        default:
          return "Pending";
      }
    },
    icon: (status: string): JSX.Element => {
      switch(status) {
        case "qualified":
          return <FaCheck size={14} className='text-white'/>;
        case "unqualified":
          return <RiLoader2Line size={18} className='text-indigo-500'/>;
        case "disqualified":
          return <IoClose size={14} className='text-white'/>;
        default:
          return <FaRegCircle size={14} className='text-white'/>;
      }
    }
  }

  return (
    <div className="flex py-4 overflow-x-auto">
      {/* {list_stages?.map((item: any, i) => {
        return (
          <div key={i} className={'flex flex-col min-w-[175px] w-[175px] gap-2 items-center ' + (i === 0 ? 'ml-auto ' : i === list_stages.length - 1 ? 'mr-auto' : '')}>
            <div className='flex w-full gap-2 items-center'>
              <div className={`flex-1 h-[3px] ${qualificationStatus.line_color(i > 0 && list_stages[i-1].qualification_status)} ${i === 0 && "opacity-0"}`}/>
              <div className={`flex w-7 h-7 rounded-full border items-center justify-center ${qualificationStatus.baloon_color(item.qualification_status)}`}>
                {qualificationStatus.icon(item.qualification_status)}
              </div>
              <div className={`flex-1 w-full h-[3px] ${qualificationStatus.line_color(item.qualification_status)} ${i === list_stages.length - 1 && "opacity-0"}`}/>
            </div>

            <Typography className={`p-2 px-4 text-xs text-center ${qualificationStatus.status_color(item.qualification_status)} rounded-full`}>{qualificationStatus.name(item.qualification_status)}</Typography>

            <Typography className={`px-4 text-sm text-center ${qualificationStatus.stage_color(item.qualification_status)}`}>{item.stage_name}</Typography>
          </div>
        )
      })} */}
      {list_stages?.map((item: any, i) => {
        return (
          <div key={i} className={'flex flex-col min-w-[100px] w-[100px] md:min-w-[175px] md:w-[175px] gap-2 items-center ' + (i === 0 ? 'ml-auto ' : i === list_stages.length - 1 ? 'mr-auto' : '')}>
            <div className='flex w-full gap-2 items-center'>
              <div className={`flex-1 h-[3px] ${qualificationStatus.line_color(i > 0 && list_stages[i-1].qualification_status)} ${i === 0 && "opacity-0"}`}/>
              <div className={`flex w-7 h-7 rounded-full border items-center justify-center ${qualificationStatus.baloon_color(item.qualification_status)}`}>
                {qualificationStatus.icon(item.qualification_status)}
              </div>
              <div className={`flex-1 w-full h-[3px] ${qualificationStatus.line_color(item.qualification_status)} ${i === list_stages.length - 1 && "opacity-0"}`}/>
            </div>

            <Typography className={`p-2 px-4 text-[0.5rem] leading-[0.75rem] md:text-xs text-center ${qualificationStatus.status_color(item.qualification_status)} rounded-full`}>{qualificationStatus.name(item.qualification_status)}</Typography>

            <Typography className={`px-4 text-xs md:text-sm text-center ${qualificationStatus.stage_color(item.qualification_status)}`}>{item.stage_name}</Typography>
          </div>
        )
      })}
      {/* {steps.map((item, index) => {
          const statusKey = item.status.toLowerCase();

          return (
            <div key={index} className={clsx('border-t-2 last:border-t-white h-40 relative', lineClasses[statusKey])}>
              <div
                className={clsx(
                  'w-8 h-8 rounded-full flex items-center justify-center absolute -inset-y-5 left-0 border-4 border-white box-content',
                  iconClasses[statusKey]
                )}
              >
                {iconType[statusKey]}
              </div>

              <div className="absolute w-32 -left-11 inset-y-7 space-y-2">
                <div className="flex items-center justify-center">
                  <p className={clsx('text-[10px] py-1 px-2.5 rounded-full', badgeClasses[statusKey])}>{item.status}</p>
                </div>
                <p className={clsx('text-center text-xs', textClasses[statusKey])}>{item.name}</p>
              </div>
            </div>
          );
        })} */}
    </div>
  );
}
