import * as React from 'react';
import { ScheduleProviderProps, TScheduleContext } from './context.types';
import { initialValueSchedule, reducerResult } from './actions/scheduleAction';

export const ScheduleContext = React.createContext<TScheduleContext | undefined>(undefined);

export const useScheduleContext = () => {
  const context = React.useContext(ScheduleContext);
  if (context === undefined) {
    throw new Error('useScheduleContext must be used within a ResultProvider');
  }
  return context;
};

export const ScheduleProvider = ({ children }: ScheduleProviderProps) => {
  const [state, dispatch] = React.useReducer(reducerResult, initialValueSchedule);

  return <ScheduleContext.Provider value={{ state, dispatch }}>{children}</ScheduleContext.Provider>;
};
