import * as React from 'react';
import { useEffect, useState } from 'react';
import { Dialog, DialogHeader, DialogBody, Tooltip, Button, Input, Textarea, Typography, DialogFooter, input } from '@material-tailwind/react';
import { clsx } from 'clsx';
import services from '../../../../../utils/services';
import { useResultContext } from '../../context/resultContext';
// import { TPopUpResult } from './PopUpDetailResult.types';
import { replacePlaceholders } from '../../utils/replace-placeholder';
import { IoCloseCircleOutline } from 'react-icons/io5';
import { CalendarBlueIcon, XmarkIcon } from '../../assets/svg';
import { FaRegFileAlt } from 'react-icons/fa';
import { AiOutlineInfoCircle } from 'react-icons/ai';
import { T_ApplicantDetail } from '../MyApplication';
import { toast } from 'react-toastify';
import { useScreeningModalContext } from '../../context/preScreeningQuestion';
import { format } from 'date-fns';
import * as dayjs from 'dayjs';
import RichTextEditor from '../RichTextEditor';
import { LuEye, LuTrash2 } from 'react-icons/lu';
import { AiOutlineLoading3Quarters } from "react-icons/ai";

type T_QuestionDetail = {
  id: string,
  question: string,
  type: string,
  start_date: string,
  end_date: string,
  value: string,
  is_disabled: boolean,
  is_submit: number
}

type T_LoadingButton = {
  submit: boolean;
  draft: boolean;
}

type T_PopUpPreScreening = {
  isOpen: boolean;
  handleClosedModalScreening: () => void;
  applicant: T_ApplicantDetail[];
}

export default function PopUpPreScreening({ isOpen, handleClosedModalScreening, applicant }: T_PopUpPreScreening) {
  const [dataResult, setDataResult] = useState<any>([]);
  const [htmlContent, setHtmlContent] = useState<string>('');
  const [formData, setFormData] = useState({ form: [{ type: '', question: '', start_date: '', end_date: '' }], isSubmit: false });
  const [applicantName, setApplicantName] = useState<string>('');
  const [userId, setUserId] = useState<string>(JSON.parse(localStorage.getItem('user')).id);

  const { state } = useResultContext();
  const {
    applicantData,
    setApplicantData,
    inputs,
    setInputs,
    questions,
    loading,
    loadingButton,
    setConfirmationModalBox,
    setIsOpenModalScreening,
    loadingQuestions,
    loadingFields,
    setLoadingFields
  } = useScreeningModalContext();

  const handleClose = () => {
    setApplicantData(null);
    handleClosedModalScreening();
  };

  // useEffect(() => {
  //   const newQ = [...questions]
  //   const arr = [];
  //   //sort by end_date
  //   questions.forEach((irem) => arr.push(new Date(irem.end_date).valueOf()));
  //   console.log(newQ?.sort((a, b) => new Date(a.end_date).valueOf() < new Date(b.end_date).valueOf() ? 1 : 0))
  //   console.log(arr.sort((a, b) => a < b ? 1 : 0))
  // }, [questions])

  useEffect(() => {
    if(localStorage.getItem('user')) {
      setApplicantName(JSON.parse(localStorage.getItem('user')).name);
    }
  }, [])

  // useEffect(() => {
  //   console.log(inputs);
  // }, [inputs]);

  const handleChange = (fieldName: string, data: string) => {
    setInputs((prev) => ({...prev, [fieldName]: data}));
  }

  const stageData = () => {
    const stage = applicantData?.list_stages.find((item) => item.vacancy_stage_id === applicantData?.last_vacancy_stage_id);
    return stage;
  }

  const base64DocTypes = (type: string): string => {
    if(type.includes(".pdf")) {
      return "application/pdf";
    } else if(type.includes(".jpeg") || type.includes(".jpg")) {
      return "image/jpeg";
    } else if(type.includes(".png")) {
      return "image/png";
    } else {
      return "";
    }
  }

  const previewFile = async (file: File | string, id: string) => {
    if(typeof file === 'object') {
      const urlFile = URL.createObjectURL(file);
      return window.open(urlFile, '_blank');
    }

    try {
      setLoadingFields((prev) => ({...prev, [id]: true}));
      const response = await services.post('/preview-file', {
        user_applicant_id: userId,
        filename: file
      })

      const w = window.open("");
      w.document.write(`<embed src="${file.includes('.pdf') ? `data:application/pdf;base64,${response.split('base64,')[1]}` : response}" type="${base64DocTypes(response)}" ${file.includes(".pdf") && `width="100%" height="100%"`}/>`)
      w.document.body.style.padding = "0";
      w.document.body.style.margin = "0";
      w.document.body.style.boxSizing = "border-box";
      window.open(response, '_blank');
    } catch (error) {
      console.log(error);
      toast.error('Failed to load file');
    } finally {
      setLoadingFields((prev) => ({...prev, [id]: false}));
    }
  }

  const onDraft = () => {
    setIsOpenModalScreening(false);
    setConfirmationModalBox((prev) => ({
      ...prev,
      isOpen: true,
      mode: 'draft',
      loading: false
    }));
  }

  const onSubmit = () => {
    setIsOpenModalScreening(false);
    setConfirmationModalBox((prev) => ({
      ...prev,
      isOpen: true,
      mode: 'submit',
      loading: false
    }));
  }

  const forms = (item: T_QuestionDetail, idx: number) => {
    switch (item.type) {
      case 'short':
        return (
          <div className='flex flex-col gap-2' key={idx}>
            <Typography className="text-black font-normal text-sm">{idx}. {item.question}</Typography>
            {(item.is_submit !== 1 && item.end_date) && <Typography
              className="w-fit ml-4 px-2 py-1 text-xs text-black font-normal bg-orange-50 border border-orange-500 rounded-full"
            >
              <span className='font-bold'>Due:</span> {dayjs(item.end_date).format('D MMM YYYY, HH:mm')}
            </Typography>}
            <div className='ml-4'>
              {item.is_submit === 1
              ? <Typography className="text-xs text-black font-normal">{`${item.value}`}</Typography>
              : <RichTextEditor
                fieldName={item.id}
                contentHtml={inputs[item.id]}
                setSelectedData={handleChange}
                maxChar={500}
              />}
            </div>
          </div>
        );
      case 'long':
        return (
          <div className='flex flex-col gap-2' key={idx}>
            <Typography className="text-black font-normal text-sm">{idx}. {item.question}</Typography>
            {(item.is_submit !== 1 && item.end_date) && <Typography
              className="w-fit ml-4 px-2 py-1 text-xs text-black font-normal bg-orange-50 border border-orange-500 rounded-full"
            >
              <span className='font-bold'>Due:</span> {dayjs(item.end_date).format('D MMM YYYY, HH:mm')}
            </Typography>}
            <div className='pl-4'>
              {item.is_submit === 1
              ? <Typography className="text-xs text-black font-normal">{`${item.value}`}</Typography>
              : <RichTextEditor
                fieldName={item.id}
                contentHtml={inputs[item.id]}
                setSelectedData={handleChange}
                isResizeable={true}
                maxChar={50000}
              />}
            </div>
          </div>
        );
      case 'image':
        return (
          <div className='flex flex-col gap-2' key={idx}>
            <Typography className="text-black font-normal text-sm">{idx}. {item.question}</Typography>
            {(item.is_submit !== 1 && item.end_date) && <Typography
              className="w-fit ml-4 px-2 py-1 text-xs text-black font-normal bg-orange-50 border border-orange-500 rounded-full"
            >
              <span className='font-bold'>Due:</span> {dayjs(item.end_date).format('D MMM YYYY, HH:mm')}
            </Typography>}
            <div className='flex flex-1 ml-4 px-2 py-1.5 gap-1 border-2 border-dashed rounded-md items-center justify-between'>
              {item.is_submit !== 1 && 
                <>
                  <input
                    id={item.id}
                    type="file"
                    accept=".jpg, .jpeg, .png"
                    onChange={(e) => {
                      if(e.target.files[0].size > 1024000) {
                        toast.error('File size must be less than 1MB');
                        return
                      }

                      const file = e.target.files[0];
                      setInputs((prev) => ({...prev, [item.id]: file}));
                      e.target.value = null;
                    }}
                    disabled={inputs[item.id]}
                    hidden
                  />
                  <label
                    htmlFor={item.id}
                    className={"px-2 py-1 text-sm font-normal bg-gray-100 rounded-md " + ((inputs[item.id]) ? "text-gray-500" : "text-black cursor-pointer hover:bg-gray-200")}
                  >
                    Choose File
                  </label>
                </>
              }

              <Typography className="flex-1 text-xs font-normal !text-gray-400 text-ellipsis text-nowrap overflow-hidden">
                {inputs[item.id] 
                  ? inputs[item.id].name || `${idx}_${stageData()?.stage_name}_${applicantName}${inputs[item.id].includes('.jpg') ? '.jpg' : '.png'}` 
                  : 'No file chosen'
                }
              </Typography>

              <div className='flex gap-2 items-center'>
                {loadingFields[item.id] 
                ? <AiOutlineLoading3Quarters size={20} className='text-black animate-spin'/>
                : <LuEye 
                    size={20} 
                    className={`${inputs[item.id] ? "text-black cursor-pointer hover:text-primary-500" : "text-gray-500"}`}
                    onClick={() => inputs[item.id] ? previewFile(inputs[item.id], item.id) : ''}
                  />
                }
                {(inputs[item.id] && item.is_submit !== 1) &&
                  <>
                    <div className='w-[1px] h-4 !bg-gray-400'/>
                    <LuTrash2 
                      size={20} 
                      className="text-black cursor-pointer hover:text-danger-500" 
                      onClick={() => setInputs((prev) => ({...prev, [item.id]: ''}))}
                    />
                  </>
                }
              </div>
            </div>
          </div>
        );
      case 'document':
        return (
          <div className='flex flex-col gap-2' key={idx}>
            <Typography className="text-black font-normal text-sm">{idx}. {item.question}</Typography>
            {(item.is_submit !== 1 && item.end_date) && <Typography
              className="w-fit ml-4 px-2 py-1 text-xs text-black font-normal bg-orange-50 border border-orange-500 rounded-full"
            >
              <span className='font-bold'>Due:</span> {dayjs(item.end_date).format('D MMM YYYY, HH:mm')}
            </Typography>}
            <div className='flex flex-1 ml-4 px-2 py-1.5 gap-1 border-2 border-dashed rounded-md items-center justify-between'>
              {item.is_submit !== 1 && 
                <>
                  <input
                    id={item.id}
                    type="file"
                    accept=".pdf"
                    onChange={(e) => {
                      if(e.target.files[0].size > 1024000) {
                        toast.error('File size must be less than 1MB');
                        return 
                      }

                      const file = e.target.files[0];
                      setInputs((prev) => ({...prev, [item.id]: file}));
                      e.target.value = null;
                    }}
                    disabled={inputs[item.id]}
                    hidden
                  />
                  <label
                    htmlFor={item.id}
                    className={"px-2 py-1 text-sm font-normal bg-gray-100 rounded-md " + ((inputs[item.id]) ? "text-gray-500" : "text-black cursor-pointer hover:bg-gray-200")}
                  >
                    Choose File
                  </label>
                </>
              }

              <Typography className="flex-1 text-xs font-normal !text-gray-400 text-ellipsis text-nowrap overflow-hidden">
                {inputs[item.id] 
                  ? inputs[item.id].name || `${idx}_${stageData()?.stage_name}_${applicantName}.pdf`
                  : 'No file chosen'
                }
              </Typography>

              <div className='flex gap-2 items-center'>
                {loadingFields[item.id] 
                ? <AiOutlineLoading3Quarters size={20} className='text-black animate-spin'/>
                : <LuEye 
                    size={20} 
                    className={`${inputs[item.id] ? "text-black cursor-pointer hover:text-primary-500" : "text-gray-500"}`}
                    onClick={() => inputs[item.id] ? previewFile(inputs[item.id], item.id) : ''}
                  />
                }
                {(inputs[item.id] && item.is_submit !== 1) && 
                  <>
                    <div className='w-[1px] h-4 !bg-gray-400'/>
                    <LuTrash2 
                      size={20} 
                      className="text-black cursor-pointer hover:text-danger-500" 
                      onClick={() => setInputs((prev) => ({...prev, [item.id]: ''}))}
                    />
                  </>
                }
              </div>
            </div>
          </div>
        );
      case 'video':
        return (
          <div className='flex flex-col gap-2' key={idx}>
            <Typography className="text-black font-normal text-sm">{idx}. {item.question}</Typography>
            {(item.is_submit !== 1 && item.end_date) && <Typography
              className="w-fit ml-4 px-2 py-1 text-xs text-black font-normal bg-orange-50 border border-orange-500 rounded-full"
            >
              <span className='font-bold'>Due:</span> {dayjs(item.end_date).format('D MMM YYYY, HH:mm')}
            </Typography>}
            <div className='w-full pl-4 leading-none'>
              {item.is_submit === 1
              ? <a
                href={`https://${item.value}`}
                target="_blank"
                className="p-0 m-0 h-fit text-blue-700 text-xs leading-none hover:underline"
              >
                https://{item.value}
              </a>
              : <div className='flex flex-1 rounded-md border items-center overflow-hidden'>
                <div className='flex px-4 py-2 h-full bg-gray-50 border-r justify-center'>
                  <Typography className="text-black text-sm font-normal">https://</Typography>
                </div>
                <div className='flex flex-1 h-full px-4 items-center'>
                  <input
                    type="text"
                    value={inputs[item.id]}
                    onChange={(e) => {
                      setInputs((prev) => ({
                        ...prev,
                        [item.id]: e.target.value.replace('https://', '')
                      }));
                    }}
                    className='w-full bg-transparent outline-none'
                  />
                </div>
              </div>}
            </div>
          </div>
        );
      default:
        break;
    }
  };

  return (
    <Dialog size="lg" open={isOpen} className="flex flex-col max-h-[90vh] overflow-hidden">
      <>
        <DialogHeader className="flex items-center justify-between space-x-2 sticky top-0 z-10 bg-gray-100 border-b">
          {loading ? (
            <SkeletonLoading height="h-10" />
          ) : (
            <div className="flex justify-between items-center text-sm w-full py-1">
              <div>
                <h3 className="font-bold text-lg">{applicantName}</h3>
                <p className="font-normal">Applied as: {applicantData?.vacancy_name}</p>
              </div>
              <div className='flex flex-col gap-1 items-end'>
                {loadingQuestions
                  ? <>
                      <div className='h-6 w-[70px] bg-gray-200 rounded-full animate-pulse'/>
                      <div className='h-4 w-[150px] bg-gray-200 rounded-md animate-pulse'/>
                    </>
                  : <>
                      {(questions.length > 0 && questions[0]?.is_submit !== null) && 
                        <Typography className={`px-4 py-0.5 text-xs font-normal text-black rounded-full border ${questions[0]?.is_submit === 1 ? "bg-success-50 border-success-700" : "bg-orange-50 border-orange-500"}`}>
                          {questions[0]?.is_submit === 1 ? "Submitted" : "Draft"}
                        </Typography>
                      }
                      {questions[0]?.end_date && <div className="font-normal text-xs">
                        <span className="font-bold">Due: </span> {dayjs([...questions].sort((a, b) => new Date(a.end_date) < new Date(b.end_date) ? 1 : 0)[0].end_date).format('D MMM YYYY, HH:mm')}
                      </div>}
                      {questions[0]?.save_date && <div className="font-normal text-xs">
                        <span className="font-bold">Submitted Date: </span> {dayjs(questions[0].save_date).format('D MMM YYYY, HH:mm')}
                      </div>}
                      {/* {(questions[0]?.end_date && (questions[0]?.is_submit && questions[0]?.is_submit !== 1)) && <div className="font-normal">
                        <span className="font-bold">Due: </span> {dayjs([...questions].sort((a, b) => new Date(a.end_date) < new Date(b.end_date) ? 1 : 0)[0].end_date).format('D MMM YYYY, HH:mm')}
                      </div>}
                      {(questions[0]?.save_date && (questions[0]?.is_submit && questions[0]?.is_submit !== 1)) && <div className="font-normal">
                        <span className="font-bold">Submitted Date: </span> {dayjs(questions[0].save_date).format('D MMM YYYY, HH:mm')}
                      </div>} */}
                    </>
                }
              </div>
            </div>
          )}
        </DialogHeader>
        <DialogBody className="flex-1 p-4 font-normal text-gray-950 overflow-y-auto overflow-x-hidden relative">
          {loading ? (
            <SkeletonLoading height="h-40" />
          ) : (
            <div>
              <div className="flex items-center gap-2 py-1 font-bold "><FaRegFileAlt /> Pre-Screening Question - {stageData()?.stage_name}</div>
              <hr />
              <div className='flex flex-col mt-4 gap-4'>
                {loadingQuestions
                  ? [1,2,3].map((_, idx) => (
                    <div className='flex flex-col gap-2' key={idx}>
                      <div className='h-4 w-[30%] bg-gray-200 rounded-md animate-pulse'/>
                      <div className='h-6 w-[150px] ml-4 bg-gray-200 rounded-full animate-pulse'/>
                      <div className='w-full pl-4'>
                        <div className='h-8 w-full bg-gray-200 rounded-md animate-pulse'/>
                      </div>
                    </div>
                  ))
                  : questions.map((item, idx) => forms(item, idx + 1))
                }
              </div>
            </div>
          )}
          {/* <Toaster position="top-center" reverseOrder={false} toastOptions={{ className: 'z-50' }} /> */}
        </DialogBody>
        <DialogFooter className="flex justify-between bg-gray-100 border-t-2">
          <Button
            variant="outlined"
            className="normal-case border-primary-500 text-primary-500"
            onClick={handleClose}
            disabled={loadingButton.draft || loadingButton.submit}
          >
            Close
          </Button>
          {questions[0]?.is_submit !== 1 &&
            <div className='flex gap-2 items-center'>
              <Button
                variant="text"
                className="normal-case text-primary-500"
                onClick={onDraft}
                disabled={loading || Object.entries(inputs).length === 0}
                loading={loadingButton.draft}
              >
                Save as a Draft
              </Button>
              <Button
                variant="filled"
                className="normal-case bg-primary-500"
                onClick={onSubmit}
                disabled={loading || Object.entries(inputs).length === 0}
                loading={loadingButton.submit}
              >
                Submit
              </Button>
            </div>
          }
        </DialogFooter>
      </>
    </Dialog>
  );
}

function SkeletonLoading({ height }: { height: string }) {
  return <div className={`${height} w-full bg-gray-200 rounded-md animate-pulse`} />;
}
