import SuccessIcon, { FailedIcon } from '../../assets/svg';
import * as React from 'react';

export default function ResultComponent({ status, result_html_template }) {
  return (
    <div className="flex flex-col items-center justify-center space-y-3">
      {status === 'qualified' ? <SuccessIcon /> : <FailedIcon />}
      <div dangerouslySetInnerHTML={{ __html: result_html_template }} />
    </div>
  );
}
