import { ActionResult, TResult } from '../context.types';

export const initialValueResult: TResult = {
  isOpen: false,
  applicant_id: '',
  vacancy_stage_id: '',
};

export const reducerResult = (state: TResult, action: ActionResult): TResult => {
  switch (action.type) {
    case 'OPEN_DETAIL_RESULT':
      return { ...state, ...action.payload };
    case 'CLOSED_DETAIL_RESULT':
      return { ...state, ...initialValueResult };
    default:
      return state;
  }
};
