import * as React from 'react';

declare global {
  interface Window {
    RichTextEditor: any;
  }
};

type T_RTE = {
  fieldName: string;
  contentHtml: string | null;
  setSelectedData: any;
  isResizeable?: boolean;
  maxChar?: number;
};

export default function RichTextEditor({ fieldName, contentHtml, setSelectedData, isResizeable = false, maxChar = -1 }: T_RTE) {
  const refdiv = React.useRef(null);
  const [rte, setRte] = React.useState<any>(null);

  React.useEffect(() => {
    if (refdiv.current && window.RichTextEditor) {
      const editorCfg = {
        toolbar: "basic",
        showFloatImageToolBbar: false,
        showPlusButton: false,
        showTagList: false,
        editorResizeMode: isResizeable ? "height" : "none",
        maxTextLength: maxChar,
      }
      const editor = new window.RichTextEditor(refdiv.current, editorCfg);
      editor.setHTMLCode(`<div style="font-family: 'Inter', sans-serif;">${contentHtml}</div>`);
      setRte(editor);

      // Adding onChange event listener
      editor.attachEvent('change', () => setSelectedData(fieldName, editor.getPlainText()));
    };

    // Cleanup function to remove event listener when component unmounts
    return () => {
      if (rte) {
        rte.detachEvent('change');
      };
    };
  }, []);

  return (
    <div ref={refdiv} />
  );
};