import * as React from 'react';
import { Dialog } from '@material-tailwind/react';
import { XmarkIcon } from '../../assets/svg';
import { clsx } from 'clsx';
import api from '../../../../../utils/axios';
import { useResultContext } from '../../context/resultContext';
import { TPopUpResult } from './PopUpDetailResult.types';
import ResultComponent from './ResultComponent';
import { replacePlaceholders } from '../../utils/replace-placeholder';

export default function PopUpResult({ isOpen, handleClosePopupResult, stage_name }: TPopUpResult) {
  const [dataResult, setDataResult] = React.useState<any>([]);
  const [htmlContent, setHtmlContent] = React.useState<string>('');
  const [isLoadingDetailResult, setIsLoadingDetaliResult] = React.useState(false);

  const { state } = useResultContext();

  const handleClose = () => {
    handleClosePopupResult();
  };

  const getResultAssessment = async () => {
    setIsLoadingDetaliResult(true);
    try {
      const response = await api.post('/myapplication/get_result_assessment', {
        applicant_id: state.applicant_id,
        vacancy_stage_id: state.vacancy_stage_id,
      });
      const result = response.data?.data[0];
      setDataResult(result);

      let { result_html_template } = result;
      result_html_template = replacePlaceholders(result_html_template, result);
      setHtmlContent(result_html_template);
    } catch (error) {
      console.error(error);
    } finally {
      setIsLoadingDetaliResult(false);
    }
  };

  React.useEffect(() => {
    if (isOpen) {
      getResultAssessment();
    }
  }, [isOpen]);

  return (
    <Dialog
      size="md"
      open={isOpen}
      className={clsx('overflow-y-auto border-2', dataResult?.final_summary === 'qualified' ? 'border-green-600' : 'border-red-600')}
    >
      <div className="bg-white z-20 font-normal text-gray-950 max-h-[40rem] overflow-y-auto relative p-5 ">
        <button className="absolute right-5" onClick={handleClose}>
          <XmarkIcon />
        </button>

        {isLoadingDetailResult ? <p>Loading ..</p> : <ResultComponent status={dataResult?.final_summary} result_html_template={htmlContent} />}
      </div>
    </Dialog>
  );
}
