import * as React from 'react';
import { useState, useEffect } from 'react';
import { T_ApplicantDetail } from '../components/MyApplication';
import { T_ModalState } from '../components/ConfirmationModal';
import services from '../../../../utils/services';
import toast from 'react-hot-toast';

type T_QuestionDetail = {
  id: string,
  question: string,
  type: string,
  start_date: string,
  end_date: string,
  value: string,
  is_disabled: boolean,
  is_submit: number,
  save_date: string,
}

type T_LoadingButton = {
  submit: boolean;
  draft: boolean;
}

type T_SuccessModalState = {
  isOpen: boolean;
  mode: "draft" | "submit";
}

type TScreeningModalContext = {
  inputs: any;
  setInputs: React.Dispatch<React.SetStateAction<any>>;
  confirmationModalBox: T_ModalState;
  setConfirmationModalBox: React.Dispatch<React.SetStateAction<T_ModalState>>;
  isOpenModalScreening: boolean;
  setIsOpenModalScreening: React.Dispatch<React.SetStateAction<boolean>>;
  applicantData: T_ApplicantDetail;
  setApplicantData: React.Dispatch<React.SetStateAction<T_ApplicantDetail>>;
  handleClosedModalScreening: () => void;
  getPrescreeningQuestion: () => void;
  submitPrescreeningDraft: () => void;
  submitPrescreeningFinal: () => void;
  questions: T_QuestionDetail[];
  setQuestions: React.Dispatch<React.SetStateAction<T_QuestionDetail[]>>;
  loading: boolean;
  setLoading: React.Dispatch<React.SetStateAction<boolean>>;
  loadingButton: T_LoadingButton;
  setLoadingButton: React.Dispatch<React.SetStateAction<T_LoadingButton>>;
  successModalState: boolean;
  setSuccessModalState: React.Dispatch<React.SetStateAction<boolean>>;
  successModalMode: "draft" | "submit";
  setSuccessModalMode: React.Dispatch<React.SetStateAction<"draft" | "submit">>;
  loadingQuestions: boolean;
  setLoadingQuestions: React.Dispatch<React.SetStateAction<boolean>>;
  loadingFields: any;
  setLoadingFields: React.Dispatch<React.SetStateAction<any>>;
  isHistory: boolean;
  setIsHistory: React.Dispatch<React.SetStateAction<boolean>>;
  historyVacancyId: string;
  setHistoryVacancyId: React.Dispatch<React.SetStateAction<string>>;
};

export const ScreeningModalContext = React.createContext<TScreeningModalContext | undefined>(undefined);

export const useScreeningModalContext = () => {
  const context = React.useContext(ScreeningModalContext);
  if (context === undefined) {
    throw new Error('useScreeningModalContext must be used within a ModalScreeningProvider');
  }
  return context;
};

type TScreeningModalProvider = {
  children: React.ReactNode;
};

export const ModalScreeningProvider = ({ children }: TScreeningModalProvider) => {
  const [isOpenModalScreening, setIsOpenModalScreening] = useState(false);
  const [applicantData, setApplicantData] = useState<T_ApplicantDetail>(null);
  const [isHistory, setIsHistory] = useState<boolean>(false);
  const [historyVacancyId, setHistoryVacancyId] = useState<string>('');
  const [confirmationModalBox, setConfirmationModalBox] = useState<T_ModalState>({
    isOpen: false,
    mode: "draft",
    loading: false
  });
  const [successModalState, setSuccessModalState] = useState<boolean>(false);
  const [successModalMode, setSuccessModalMode] = useState<"draft" | "submit">("draft");
  const [inputs, setInputs] = useState<any>({});
  const [questions, setQuestions] = useState<T_QuestionDetail[]>([]);
  const [loading, setLoading] = useState<boolean>(false);
  const [loadingButton, setLoadingButton] = useState<T_LoadingButton>({draft: false, submit: false});
  const [loadingQuestions, setLoadingQuestions] = useState<boolean>(false);
  const [loadingFields, setLoadingFields] = useState<any>({});

  useEffect(() => {
    // console.log(applicantData);
    if(applicantData) {
      getPrescreeningQuestion();
    }
  }, [applicantData, historyVacancyId])

  const handleClosedModalScreening = () => {
    setIsOpenModalScreening(false);
  };

  const getPrescreeningQuestion = async () => {
    try {
      setQuestions([]);
      setLoadingQuestions(true);
      const response = await services.post('/myapplication/get_prescreening_question', {
        applicant_id: applicantData?.applicant_id,
        vacancy_stage_id: isHistory ? historyVacancyId : applicantData?.last_vacancy_stage_id,
      });

      if(response.statusCode === 200) {
        // console.log(response);
        setQuestions(response.data);
        response.data.forEach((item: T_QuestionDetail) => {
          setInputs((prev) => ({...prev, [item.id]: item.value}));

          if(item.type === 'image' || item.type === 'document') {
            setLoadingFields((prev) => ({...prev, [item.id]: false}));
          }
        });
      }
    } catch (error) {
      console.log(error);
      toast.error('Failed to get prescreening question');
    } finally {
      setLoadingQuestions(false);
    }
  }

  const uploadFile = async (file: File): Promise<string> => {
    try {
      setLoading(true);
      const response = await services.post('/myapplication/upload-file', {
        file,
        applicant_id: applicantData.applicant_id,
      }, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });

      if(response.statusCode === 200) {
        // console.log(response);
        return response.file;
      }
    } catch (error) {
      console.error(error);
      toast.error('Failed to upload some files');
      return 'failed';
    } finally {
      setLoading(false);
    }
  }

  const submitPrescreeningDraft = async () => {
    try {
      setLoading(true);
      setLoadingButton((prev) => ({...prev, draft: true}));
      setConfirmationModalBox((prev) => ({...prev, loading: true}));
      const data = await Promise.all(
        Object.entries(inputs).map(async ([key, value]) => {
          if (typeof value === 'object') {
            const file = await uploadFile(value as File);

            return {
              id: key,
              value: file
            };
          }
  
          return {
            id: key,
            value
          };
        })
      );
  
      // console.log(data);

      const response = await services.post('/myapplication/submit_prescreening_question', {
        applicant_id: applicantData.applicant_id,
        vacancy_stage_id: applicantData.last_vacancy_stage_id,
        data,
        is_submit: '0',
      });

      if(response.statusCode === 200) {
        setSuccessModalMode('draft');
        setSuccessModalState(true);
      }
    } catch (error) {
      console.error(error);
      toast.error('Failed to submit prescreening as a draft');
    } finally {
      setLoading(false);
      setLoadingButton((prev) => ({...prev, draft: false}));
      setConfirmationModalBox((prev) => ({...prev, isOpen: false, loading: true}));
    }
  }

  const submitPrescreeningFinal = async () => {
    try {
      setLoading(true);
      setLoadingButton((prev) => ({...prev, submit: true}));
      setConfirmationModalBox((prev) => ({...prev, loading: true}));
      const data = await Promise.all(
        Object.entries(inputs).map(async ([key, value]) => {
          if (typeof value === 'object') {
            const file = await uploadFile(value as File);

            return {
              id: key,
              value: file
            };
          }
  
          return {
            id: key,
            value
          };
        })
      );
  
      // console.log(data);

      const response = await services.post('/myapplication/submit_prescreening_question', {
        applicant_id: applicantData.applicant_id,
        vacancy_stage_id: applicantData.last_vacancy_stage_id,
        data,
        is_submit: '1',
      });

      if(response.statusCode === 200) {
        setSuccessModalMode('submit');
        setSuccessModalState(true);
      }
    } catch (error) {
      console.error(error);
      toast.error('Failed to submit prescreening as a draft');
    } finally {
      setLoading(false);
      setLoadingButton((prev) => ({...prev, submit: false}));
      setConfirmationModalBox((prev) => ({...prev, isOpen: false, loading: true}));
    }
  }
  

  const value = {
    inputs,
    setInputs,
    confirmationModalBox,
    setConfirmationModalBox,
    isOpenModalScreening,
    setIsOpenModalScreening,
    applicantData,
    setApplicantData,
    handleClosedModalScreening,
    getPrescreeningQuestion,
    submitPrescreeningDraft,
    submitPrescreeningFinal,
    questions,
    setQuestions,
    loading,
    setLoading,
    loadingButton,
    setLoadingButton,
    successModalState,
    setSuccessModalState,
    successModalMode,
    setSuccessModalMode,
    loadingQuestions,
    setLoadingQuestions,
    loadingFields,
    setLoadingFields,
    isHistory,
    setIsHistory,
    historyVacancyId,
    setHistoryVacancyId
  };

  return (
    <ScreeningModalContext.Provider value={value}>
      {children}
    </ScreeningModalContext.Provider>
  );
};