import * as React from 'react';
import CvComplete from './CvComplete';
import CvNotCompplete from './CvNotCompplete';

type TNotYetApplyComponent = {
  isComplete: any;
};

export default function NotYetApplyComponent({ isComplete }: TNotYetApplyComponent) {
  return <div className="bg-white rounded-lg min-h-52 text-center py-20">{isComplete ? <CvComplete /> : <CvNotCompplete />}</div>;
}
