/* Ini akan mereplace semua tanda $ dengan data yang sesuai dari value api */
export const replacePlaceholders = (template: string, data: any, is_show_gmaps?: number | string | null) => {
  // Replace all placeholders with corresponding data
  let result = template.replace(/\$(\w+)/g, (match: string, p1: string) => {
    // p1 adalah teks tanpa tanda $
    return data[p1] !== undefined ? data[p1] : match;
  });

  // Manipulasi row berdasarkan data.location
  if (!data?.location) {
    result = result.replace('<tr id="location"', '<tr id="location" class="hidden"');
  } else {
    result = result.replace('<tr id="location" class="hidden"', '<tr id="location"');
  }

  // Manipulasi iframe berdasarkan is_show_gmaps
  if (is_show_gmaps === 0) {
    result = result.replace('<div style="padding-top: 24px; padding-bottom: 60px;" id="gmaps">', '<div class="hidden" id="gmaps">');
  } else {
    result = result.replace('<div class="hidden" id="gmaps">', '<div style="padding-top: 24px; padding-bottom: 60px;" id="gmaps">');
  }

  return result;
};
