import { ActionSchedule, TSchedule } from '../context.types';

export const initialValueSchedule: TSchedule = {
  isOpen: false,
  applicant_id: '',
  vacancy_stage_id: '',
};

export const reducerResult = (state: TSchedule, action: ActionSchedule): TSchedule => {
  switch (action.type) {
    case 'OPEN_DETAIL_SCHEDULE':
      return { ...state, ...action.payload };
    case 'CLOSED_DETAIL_SCHEDULE':
      return { ...state, ...initialValueSchedule };
    default:
      return state;
  }
};
