import * as React from 'react';
import CardApplication from './CardApplication';
import api from '../../../../utils/axios';
import ErrorNetwork from './ErrorNetwork';
import NotYetApplyComponent from './NotYetApplyComponent';
import PopUpScheduleDetail from './PopUpDetailJadwal';
import { useScheduleContext } from '../context/scheduleContext';
import { useResultContext } from '../context/resultContext';
import { useScreeningModalContext } from '../context/preScreeningQuestion';
import PopUpResult from './PopUpDetailResult';
import PopUpPreScreening from './PopUpPreScreeningQuestion';
import ConfirmationModal from './ConfirmationModal';
import SuccessModal from './SuccessModal';

export type T_ApplicantDetail = {
  applicant_id: string,
  vacancy_id: string,
  vacancy_name: string,
  company_name: string,
  applied_at: string,
  show_stages: boolean,
  last_vacancy_stage_id: string,
  last_stage_type: string,
  company_logo: string,
  placement_region_name: string,
  count_schedule: number,
  has_result_assessment: boolean,
  has_prescreening_question: boolean,
  list_stages: T_StageDetail[]
}

type T_StageDetail = {
  vacancy_stage_id: string,
  stage_name: string,
  sort: number,
  qualification_status: string,
  type: string,
  count_schedule: number,
  has_result_assessment: boolean
}

export default function MyApplication() {
  const [applicants, setApplicants] = React.useState<T_ApplicantDetail[]>([]);
  const [isLoading, setIsLoading] = React.useState(true);
  const [error, setError] = React.useState<any>(false);
  const [isCompleteCv, setIsCompleteCv] = React.useState<boolean>(false);

  const { state, dispatch } = useScheduleContext();
  const { state: stateResult, dispatch: dispatchResult } = useResultContext();
  const {
    isOpenModalScreening,
    handleClosedModalScreening,
    confirmationModalBox,
    setConfirmationModalBox,
    submitPrescreeningDraft,
    submitPrescreeningFinal,
    setIsOpenModalScreening,
    successModalState,
    setSuccessModalState,
    successModalMode,
    setSuccessModalMode
  } = useScreeningModalContext();

  React.useEffect(() => {
    getMyApplication();
  }, []);

  const getMyApplication = async () => {
    setIsLoading(true);
    try {
      const response = await api.post('/myapplication/onboarding', {});
      const result = response.data.data;
      console.log(result);
      // localStorage.setItem('applicant_id', result[]);
      if (result.length === 0) {
        await checkApplicationForm();
        return;
      }

      setApplicants(result);
    } catch (error) {
      console.log('🐙 ~ getMyApplication ~ error:', error);
      setError(error);
    } finally {
      setIsLoading(false);
    }
  };

  const checkApplicationForm = async () => {
    try {
      const response = await api.post('/myapplication/check_application_form', {});
      const result = response.data;

      if (result.statusCode === 200) {
        setIsCompleteCv(result.is_complete);
      }
    } catch (error) {
      console.error(error);
    }
  };

  /**
   * Pop up detail jadwal
   * Menggunakan Global state useContext
   */

  const handleOpenScheduleDetail = (applicant_id: string) => {
    dispatch({ type: 'OPEN_DETAIL_SCHEDULE', payload: { isOpen: true, applicant_id } });
  };

  const handleCloseScheduleDetail = () => {
    dispatch({ type: 'CLOSED_DETAIL_SCHEDULE' });
  };

  /**
   * Pop up detail result
   * Menggunakan Global state useContext
   */

  const handleCloseDetailResult = () => {
    dispatchResult({ type: 'CLOSED_DETAIL_RESULT' });
  };

  // if (isLoading) return <div>Loading ...</div>;

  if (error) return <ErrorNetwork />;

  return (
    <React.Fragment>
      <section className="pl-0 md:pl-14 pb-5 pt-4 md:pt-10 pr-0 md:pr-5 mx-5 md:mx-10">
        <h1 className="font-bold text-base md:text-lg mb-5">My Application</h1>
        <div className="space-y-4">
          {isLoading && <div className="w-full h-[250px] bg-gray-200 rounded-[10px] animate-pulse" />}
          {!isLoading && applicants.length === 0 && <NotYetApplyComponent isComplete={isCompleteCv} />}
          {!isLoading &&
            applicants.length > 0 &&
            applicants?.map((item: any, index) => {
              return (
                <React.Fragment key={item.applicant_id}>
                  <CardApplication key={item.applicant_id} data={item} handleOpenScheduleDetail={() => handleOpenScheduleDetail(item.applicant_id)} />
                </React.Fragment>
              );
            })}
        </div>
      </section>

      {state.isOpen && <PopUpScheduleDetail isOpen={state.isOpen} handleCloseScheduleDetail={handleCloseScheduleDetail} />}

      {stateResult.isOpen && <PopUpResult isOpen={stateResult.isOpen} handleClosePopupResult={handleCloseDetailResult} />}

      <PopUpPreScreening
        isOpen={isOpenModalScreening}
        handleClosedModalScreening={handleClosedModalScreening}
        applicant={applicants}
      />

      <ConfirmationModal
        modalState={confirmationModalBox}
        setModalState={setConfirmationModalBox}
        action={confirmationModalBox.mode === "submit" ? submitPrescreeningFinal : submitPrescreeningDraft}
        cancelAction={() => setIsOpenModalScreening(true)}
      />

      <SuccessModal
        openModal={successModalState}
        setOpenModal={setSuccessModalState}
        mode={successModalMode}
        closeAction={getMyApplication}
      />
    </React.Fragment>
  );
}
