import * as React from 'react';
import { useState } from 'react';
import { Button } from '@material-tailwind/react';
import { GoChecklist } from "react-icons/go";
import { useScreeningModalContext } from '../../context/preScreeningQuestion';

type T_StageDetail = {
  vacancy_stage_id: string,
  stage_name: string,
  sort: number,
  qualification_status: string,
  type: string,
  count_schedule: number,
  has_result_assessment: number,
  has_prescreening_question: number
}

const HistoryPrescreeningComponent = ({data, list_stages, applicant_id}) => {
  const { setApplicantData, setIsOpenModalScreening, setIsHistory, setHistoryVacancyId } = useScreeningModalContext();
  
  return (
    <div className="flex flex-col pb-4 gap-3">
        <h3 className="text-gray-500 font-semibold">Prescreening History</h3>
        <div className='flex flex-col gap-2'>
          {list_stages.every((stage: T_StageDetail) => stage.has_prescreening_question === 0)
          ? <span className='text-sm text-gray-400'>- No history prescreening test</span>
          : list_stages.map((stage: T_StageDetail) => stage.has_prescreening_question 
            ? <div className='flex px-4 py-2 gap-4 bg-gray-50 rounded-md items-center justify-between'>
                <div className='flex gap-2 items-center'>
                  <GoChecklist size={20} className='text-black'/>
                  <span>{stage.stage_name}</span>
                </div>

                <Button
                  variant="outlined"
                  size="sm"
                  className="normal-case font-medium text-primary-500 border-primary-500 bg-white rounded-full"
                  onClick={() => {
                    setApplicantData(data);
                    setIsOpenModalScreening(true);
                    setIsHistory(true);
                    setHistoryVacancyId(stage.vacancy_stage_id);
                  }}
                >
                  View Detail
                </Button>
              </div>
            : ''
          )}
        </div>
    </div>
  )
}

export default HistoryPrescreeningComponent