import * as React from 'react';
import { Typography } from '@material-tailwind/react';
import { LocationIcon } from '../assets/svg';
import ButtonBadge from './ButtonBadge';
import FooterCard from './FooterCard';
import FireGif from '../assets/gif/fire.gif';

import { useResultContext } from '../context/resultContext';
import { useScreeningModalContext } from '../context/preScreeningQuestion';

type TCardApplication = {
  data: any;
  handleOpenScheduleDetail: () => void;
};

export default function CardApplication({ data, handleOpenScheduleDetail }: TCardApplication) {
  const {
    applicant_id,
    vacancy_id,
    vacancy_name,
    company_name,
    applied_at,
    show_stages,
    placement_region_name,
    list_stages,
    count_schedule,
    last_stage_type,
    has_result_assessment,
    last_vacancy_stage_id,
    company_logo,
    has_prescreening_question,
  } = data;

  const [parsePlacement_region_name, setParsePlacement_region_name] = React.useState<[] | any>([]);

  React.useEffect(() => {
    if (data) {
      const parsePlacement_region_name = JSON.parse(placement_region_name);
      setParsePlacement_region_name(parsePlacement_region_name);

      // const filteredStages = list_stages.filter((stageFilter) => stageFilter.has_result_assessment > 0);
      // setStagesResult(filteredStages);
    }
  }, [data]);

  /**
   * History
   * result assessment
   */
  const { state: stateResult, dispatch: dispatchResult } = useResultContext();

  const handleOpenPopupResult = (last_vacancy_stage_id: string) => {
    dispatchResult({ type: 'OPEN_DETAIL_RESULT', payload: { isOpen: true, applicant_id: applicant_id, vacancy_stage_id: last_vacancy_stage_id } });
  };

  const { setIsOpenModalScreening, setApplicantData, getPrescreeningQuestion } = useScreeningModalContext();

  return (
    <React.Fragment>
      <div className="bg-white shadow-md rounded-[10px] px-[18px] py-[19px] border-[1px] min-h-40 flex flex-col justify-between">
        <div className="flex-1 flex-col justify-between">
          <div className="flex items-start space-x-2 justify-between">
            <div className="leading-loose">
              <h3 className="text-slate-900 font-semibold text-sm md:text-base">{vacancy_name}</h3>
              <p className="text-gray-400 text-xs">{company_name}</p>
              {parsePlacement_region_name.length > 0 && (
                <p className="text-xs text-slate-800 mt-3 inline-flex items-center space-x-2">
                  <LocationIcon />
                  {JSON.parse(placement_region_name)?.map((item: string) => (
                    <span key={item}>{item}</span>
                  ))}
                </p>
              )}
            </div>

            <img src={company_logo} alt="logo" className="max-w-sm max-h-10 object-cover" />
          </div>

          {has_prescreening_question === 1 && 
            <div className='flex mt-3 gap-1 items-center'>
                <img src={FireGif} alt="fire-animation" className='h-6' />
                <Typography className='px-2 py-1 text-xs text-black font-normal bg-danger-100'>Jangan lupa untuk mengerjakan Pre-Screening Question.</Typography>
            </div>
          }

          <div className="flex items-center flex-wrap mt-3 gap-2">
            {has_prescreening_question === 1 && (
              <React.Fragment>
                <ButtonBadge
                  text={<span className='font-bold'>Pre Screening Question</span>}
                  typeIcon="screening"
                  onClick={() => {
                    setApplicantData(data);
                    setIsOpenModalScreening(true);
                  }}
                />
              </React.Fragment>
            )}
            {count_schedule > 0 && (
              <ButtonBadge
                text={last_stage_type === 'assessment' ? 'Jadwal Test' : 'Jadwal Interview'}
                typeIcon="schedule"
                onClick={handleOpenScheduleDetail}
              />
            )}
            {/* {has_result_assessment === 1 && <ButtonBadge text={last_stage_type === 'assessment' && 'Hasil Online Test'} typeIcon="result" />} */}
            {has_result_assessment === 1 && (
              <React.Fragment>
                <ButtonBadge
                  text={`${last_stage_type === 'assessment' && 'Hasil Test'}`}
                  typeIcon="result"
                  onClick={() => handleOpenPopupResult(last_vacancy_stage_id)}
                />
              </React.Fragment>
            )}
          </div>
        </div>

        <FooterCard
          show_stages={show_stages}
          list_stages={list_stages}
          applied_at={applied_at}
          last_vacancy_stage_id={last_vacancy_stage_id}
          applicant_id={applicant_id}
          has_prescreening_question={has_prescreening_question}
          data={data}
        />
      </div>
    </React.Fragment>
  );
}
