import * as React from 'react';
import { clsx } from 'clsx';
import { ButtonBadgeProps } from './ButtonBadge.types';
import { CalendarIcon, ResultTestIcon, ScreeningIcon } from '../../assets/svg';
import { LuCalendarClock } from "react-icons/lu";
import { GoChecklist } from "react-icons/go";
import { PiCalendarDots } from "react-icons/pi";

const ButtonBadge: React.FC<ButtonBadgeProps> = ({ text, typeIcon, ...props }) => {
  return (
    <button
      {...props}
      className={clsx(
        'px-3 py-2 flex items-center justify-center space-x-[10px] rounded-[10px] border active:scale-95 duration-150 max-w-52',
        typeIcon === 'schedule' ? 'bg-success-50/50 border-success-700' : typeIcon === 'screening' ? 'bg-orange-50/50 border-orange-700' : 'bg-blue-50/50 border-blue-700'
      )}
    >
      {typeIcon === 'schedule' 
      ? <PiCalendarDots size={18} className='text-gray-600'/> 
      : typeIcon === 'screening' 
      ? <GoChecklist size={18} className='text-gray-600'/> 
      : <GoChecklist size={18} className='text-gray-600'/>
      }
      <p className="text-xs text-gray-600 font-bold">{text}</p>
    </button>
  );
};

export default ButtonBadge;
