import * as React from 'react';
import { TFooterCard } from './FooterCard.type';
import { format, parseISO } from 'date-fns';
import { ClockIcon, NotSuitableIcon, ProgressIcon } from '../../assets/svg';
import { clsx } from 'clsx';
import StepperApplication from '../StepperApplication';
import HistoryComponent from '../HistoryComponent';
import HistoryPrescreeningComponent from '../HistoryPrescreeningComponent';

export default function FooterCard({ show_stages, list_stages, applied_at, last_vacancy_stage_id, applicant_id, has_prescreening_question, data }: TFooterCard) {
  const [isOpenApplicationProgress, setIsOpenApplicationProgress] = React.useState<boolean>(false);
  const [isOpenHistory, setIsOpenHistory] = React.useState<boolean>(false);
  const [isOpenHistoryPrescreening, setIsOpenHistoryPrescreening] = React.useState<boolean>(false);

  const [isDisqualified, setIsDisqualified] = React.useState<null | Boolean>(null);

  React.useEffect(() => {
    if (list_stages) {
      const checkDisqualified = list_stages.some((stage) => stage.qualification_status === 'disqualified');
      setIsDisqualified(checkDisqualified);
    }
  }, [list_stages]);

  const formattedDate = (dateStr: string) => {
    const date = parseISO(dateStr);
    return format(date, 'dd-MM-yyyy HH:mm:ss'); // 'mm'
  };

  /**
   * Handler untuk stepper
   */
  const handlerOpenApplicationProgress = () => {
    setIsOpenApplicationProgress(true);
  };

  const handlerCloseApplicationProgress = () => {
    setIsOpenApplicationProgress(false);
  };

  const handlerOpenHistory = async () => {
    setIsOpenHistory(true);
  };

  const handlerCloseHistory = () => {
    setIsOpenHistory(false);
  };

  const handlerOpenHistoryPrescreening = async () => {
    setIsOpenHistoryPrescreening(true);
  };

  const handlerCloseHistoryPrescreening = () => {
    setIsOpenHistoryPrescreening(false);
  };

  return (
    <footer className="mt-2 pt-2 border-t">
      <div className="flex md:hidden items-center space-x-2 mb-3">
        <ClockIcon />
        {applied_at && <p className="text-xs text-slate-800">Applied on {formattedDate(applied_at)}</p>}
      </div>

      {!isOpenApplicationProgress && !isOpenHistory && !isOpenHistoryPrescreening && (
        <div className="flex items-center justify-between">
          {show_stages === 1 ? (
            <div className="flex items-center space-x-2">
              <button
                onClick={handlerOpenApplicationProgress}
                className={clsx(' text-sm font-semibold', isDisqualified ? 'text-red-600' : 'text-blue-700')}
              >
                {isDisqualified ? (
                  <div className="flex items-center space-x-1">
                    <div className={'p-1.5 rounded-full bg-red-600 inline-flex'}>
                      <NotSuitableIcon />
                    </div>
                    <p className="text-xs md:text-sm">Not Suitable</p>
                  </div>
                ) : (
                  <p className="text-xs md:text-sm">See Application Progress</p>
                )}
              </button>

              <p>•</p>
              <button onClick={handlerOpenHistory} className="text-gray-700 text-xs md:text-sm font-semibold">
                View History
              </button>
              {has_prescreening_question === 1 &&
                <>
                  <p>•</p>
                  <button onClick={handlerOpenHistoryPrescreening} className="text-gray-700 text-xs md:text-sm font-semibold">
                    View Prescreening History
                  </button>
                </>
              }
            </div>
          ) : (
            <p className="inline-flex space-x-1.5">
              <ProgressIcon />

              <span className="text-xs font-semibold text-[#4142D8]">
                The recruitment process is ongoing. We will notify you once there is an update.
              </span>
            </p>
          )}

          <div className="md:flex items-center space-x-2 hidden">
            <ClockIcon />
            {applied_at && <p className="text-xs text-slate-800">Applied on {formattedDate(applied_at)}</p>}
          </div>
        </div>
      )}

      {show_stages === 1 && (
        <React.Fragment>
          {/* Stepper Component */}
          <div className={clsx('transition-open', isOpenApplicationProgress ? 'max-h-screen' : 'max-h-0')}>
            {isOpenApplicationProgress && (
              <div className="pt-7 duration-200">
                <StepperApplication list_stages={list_stages} />
              </div>
            )}
          </div>

          {/* History Component */}
          <div className={clsx('transition-open', isOpenHistory ? 'max-h-screen' : 'max-h-0')}>
            {isOpenHistory && <HistoryComponent list_stages={list_stages} applicant_id={applicant_id} />}
          </div>

          {/* History Prescreening Component */}
          <div className={clsx('transition-open', isOpenHistoryPrescreening ? 'max-h-screen' : 'max-h-0')}>
            {isOpenHistoryPrescreening && <HistoryPrescreeningComponent data={data} list_stages={list_stages} applicant_id={applicant_id} />}
          </div>

          {(isOpenApplicationProgress || isOpenHistory || isOpenHistoryPrescreening) && (
            <div className="border-t pt-4 text-end">
              <button
                onClick={() => {
                  handlerCloseApplicationProgress();
                  handlerCloseHistory();
                  handlerCloseHistoryPrescreening();
                }}
                className="font-semibold text-xs text-blue-700"
              >
                See Less
              </button>
            </div>
          )}
        </React.Fragment>
      )}
    </footer>
  );
}
