import * as React from 'react';
import { Dialog, Button, Typography } from '@material-tailwind/react';
import handshakeImg from '../../../../../public/assets/images/handshake.svg';
import { useScreeningModalContext } from '../../context/preScreeningQuestion';

type T_SuccessModal = {
  openModal: boolean;
  setOpenModal: (open: boolean) => void;
  mode: "draft" | "submit";
  closeAction?: () => void;
}

const SuccessModal: React.FC<T_SuccessModal> = ({openModal, setOpenModal, mode, closeAction}) => {
  const { setQuestions, setApplicantData } = useScreeningModalContext();
  
  const onClose = () => {
    setApplicantData(null);
    setOpenModal(false);
    setQuestions([]);
    if (closeAction) closeAction();
  }
  
  return (
    <Dialog 
      open={openModal}
      size="sm"
      className="flex flex-col overflow-hidden"
    >
      <div className='flex p-4 bg-yellow-50 justify-center'>
        <img src={handshakeImg} alt="handshake-img" />
      </div>

      <div className='flex flex-col px-4 py-10 gap-4 items-center'>
        <Typography className="text-black text-2xl font-bold text-center">
          {mode === "submit"
          ? "Successfully Submit!"
          : "Draft Successfully Saved!"}
        </Typography>
        <Typography className="text-black text-center font-normal">
          {mode === "submit"
          ? "Your prescreening has been successfully submitted."
          : "Your prescreening has been successfully saved as a draft."}
        </Typography>

        <Button
          variant="filled"
          size="lg"
          color="blue"
          className="w-full max-w-[200px] normal-case text-lg bg-primary-500"
          onClick={onClose}
        >
          Close
        </Button>
      </div>
    </Dialog>
  )
}

export default SuccessModal