import { parse, isPast } from 'date-fns';
import { id } from 'date-fns/locale';

export const compareDate = (schedule_detail: any): boolean => {
  const schedule_date = schedule_detail.schedule_date_ori;
  const schedule_end_time = schedule_detail.end_time;

  // Menggabungkan tanggal dan waktu ke dalam satu string
  const dateTimeString = `${schedule_date} ${schedule_end_time}`;

  // Parsing string tanggal dan waktu menjadi objek Date
  const scheduleDateTime = parse(dateTimeString, 'yyyy-MM-dd HH:mm:ss', new Date(), { locale: id });

  // Memeriksa apakah scheduleDateTime sudah melebihi waktu saat ini
  return isPast(scheduleDateTime);
};
